.contenido-central{padding-left:0 !important;background:#f9f9fa}.contenido-central .info-mail{margin:10px 10px 10px 0;padding:15px;border:1px solid #e0e0e0}.contenido-central .info-mail .sim-mail{width:100%}.contenido-central .info-mail .sim-mail li{border-bottom:1px solid #e0e0e0;line-height:20px;margin-bottom:10px;color:#001978;font-weight:500}.contenido-central .info-mail .sim-mail li .contactos{color:#001978;text-decoration:underline}.pasos-firma{color:#001978;font-size:22px;text-transform:uppercase;margin:15px;padding-left:250px}.pasos-firma .producto-procedente{color:#001278;opacity:0.7;margin-right:15px}.pasos-firma .miga{font-size:16px;margin-left:20px;padding-left:25px;border-left:1px solid #001978}.divheader{background-color:#f4f4f4;border-bottom:1px solid rgba(0,0,0,0.12)}.topBar{flex-wrap:wrap;font-family:Roboto;font-size:16px;position:relative;z-index:2;justify-content:space-between;display:flex;box-sizing:border-box;padding:0 16px;width:100%;z-index:10;flex-direction:row;align-items:center;white-space:nowrap;margin-left:0px;width:calc(100% - 57px);transition:margin 0ms,width 0ms}.topBar.with-custom-styles{background-color:#767676}@media screen and (min-width: 801px){.topBar.with-side-bar{margin-left:240px;width:calc(100% - 299px)}}@media (max-width: 1187px) and (min-width: 801px){.topBar.with-side-bar{margin-left:240px;width:calc(100% - 298px)}}@media (max-width: 800px) and (min-width: 500px){.topBar.with-side-bar{margin-left:240px;width:calc(100% - 293px)}}
