.widget{border-radius:3px;background-color:#e6e9f2;border:1px solid #cfd3e1;-webkit-box-shadow:0px 0px 7px 0px #dde0ed;-moz-box-shadow:0px 0px 7px 0px #dde0ed;box-shadow:0px 0px 7px 0px #dde0ed;padding:10px;margin:10px 10px 20px 10px}.widget .title-icon{font-size:24px;vertical-align:bottom;color:#001978;margin-right:8px}.widget .generic-title{color:#001978;text-transform:uppercase;font-size:16px;font-weight:700}.widget .icon-trash{color:#001978;font-size:16px}.widget .subtitle{line-height:1.2;font-weight:500;font-size:15px;display:block;margin-block-start:1em;margin-block-end:1em;margin-inline-start:0px;margin-inline-end:0px}.widget form{margin:15px 0}.widget form label{margin-bottom:0 !important}.widget form label *{color:#001978}.widget form input[type='radio']{position:relative !important}.widget form .span{position:relative;padding-left:10px;cursor:pointer;display:inline-block;height:25px;line-height:25px;font-size:1rem;transition:.28s ease;user-select:none}.widget form .with-gap:checked+span:before{border:1px solid #001978}.widget form p.form{margin:5px 0;line-height:1.2}.sign{padding:15px 10px;border-top:1px solid #dadde9;margin-top:15px}.sign a{font-size:15px;color:#001978;text-decoration:underline}.bold{line-height:1.2;font-weight:500;font-size:12px}.right{float:right}.p10{padding:10px}
